import * as React from "react";
import { CreateTask, CreateTaskButton } from "./styled";

export type FloatBottomBarProps = React.PropsWithChildren<{
  onClick: any;
  label?: string;
}>;

export const FloatBottomBar = ({
  onClick,
  children,
  label = "Add item to list",
}: FloatBottomBarProps) => {
  if (children) {
    return <CreateTask>{children}</CreateTask>;
  }

  return (
    <CreateTask>
      <FloatBottomBarButtonInput onClick={onClick}>
        {label}
      </FloatBottomBarButtonInput>
    </CreateTask>
  );
};

export const FloatBottomBarButtonInput = ({
  children,
  onClick,
}: React.PropsWithChildren<{ onClick: any }>) => (
  <CreateTaskButton type="input" onClick={onClick}>
    {children}
  </CreateTaskButton>
);

export const FloatBottomBarButtonAddon = ({
  children,
  onClick,
}: React.PropsWithChildren<{ onClick: any }>) => (
  <CreateTaskButton type="addon" onClick={onClick}>
    {children}
  </CreateTaskButton>
);
